.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #333;
    color: white;
    padding: 10px;
    position: sticky;
    top: 0;
}

.navbar a {
    text-decoration: none;
    color: white !important;
    margin: 0 10px;
    font-weight: normal !important;
}

a.active {
    font-weight: bold !important;
    text-decoration: underline;
}